.actions {
    margin-bottom: 5px;
    min-height: 64px;
}

.action-btn, .tabs a {
    background: #c2c2c2;
    color: #111;
    border: 0;
    padding: 5px 8px;
    font-weight: 700;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
}

textarea {
    width: 100%;
    height: 100px;
    min-height: 25vh;
    padding: 10px;
    border: 0;
    border-top: 3px solid #c2c2c2;
    background: #fff;
    display: block;
}


.bg-clr{
    background: #e0e0e0;
}